	module.exports = {"data":{"postsJson":{"title":"Introducción a Lucas","questionsTitle":null,"display":"none","bookName":"Lucas","ref":null,"html":"<h6>Un Salvador para todos los pueblos</h6><p>El acontecimiento central de la historia: Dios interviene para buscar y salvar a los perdidos</p><h6>Cómo leer Lucas</h6><p>Cada Evangelio tiene un matiz particular a medida que cada autor presenta aspectos únicos de la vida y ministerio de Jesús. Lucas, uno de los discípulos de Pablo, era un médico gentil de la provincia romana de Macedonia. Él no experimentó los eventos de primera mano, pero recopiló los hechos de las personas que habían sido testigos oculares (Luc 1:1-3). Su gran investigación trae a la luz algunas valiosas historias que no son relatadas en los otros evangelios. Lucas quería asegurarse de contar la historia completa, incluyendo detalles del nacimiento de Jesús, su vida, muerte y resurrección.<br />   Lucas es un Evangelio muy personal. Observamos a muchos individuos teniendo encuentros personales con Jesús que transformaron sus vidas. Personas que muchas veces eran marginadas y rechazadas por la sociedad religiosa: mujeres, extranjeros, enfermos, perdidos, quebrantados, pobres, necesitados, personas en sufrimiento, indefensos y despreciados. Solo Lucas registra la parábola del buen samaritano e incluye además tres parábolas que ubica en el corazón de su narrativa, en el capítulo 15. Estas son las parábolas de la moneda perdida, la oveja perdida y el hijo pródigo. Las tres destacan el mensaje central del libro que puede ser resumido en las palabras de Jesús: «Pues el Hijo del Hombre vino a buscar y a salvar a los que están perdidos.» (Luc 19:10).</p><h6>¿Quién escribió este libro y cuándo?</h6><p>Lucas, médico gentil y compañero del apóstol Pablo lo escribió probablemente entre el año 59 y el 63 d. C. También escribió el libro de los Hechos de los Apóstoles, en el Nuevo Testamento. Esta obra de dos volúmenes posee una interesante organización geográfica.</p><br /><table style='max-width:500px; margin: 0 auto; font-size: 13px'> <tr> <th style='width:35%'>Referencia</th> <th>Organización Geográfico</th> </tr><tr> <th>Lucas 1:1-4:13</th> <th>Nacimiento, infancia y bautismo de Jesús</th> </tr><tr> <th>Lucas 4:14-9:50</th> <th>Ministerio de Jesús en Galilea y alrededores</th> </tr><tr> <th>Lucas 9:51-19:35</th> <th>Ministerio de Jesús camino a Jerusalén (ver Luc 9:53; 10:38; 13:22; 17:11; 18:31; 19:11, 28)</th> </tr><tr> <th>Lucas 19:36-24:53</th> <th>Ministerio de Jesús en Jerusalén</th> </tr><tr> <th>Hechos 1:1-7:60</th> <th>Ministerio de la iglesia en Jerusalén</th> </tr><tr> <th>Hechos 8:1-12:25</th> <th>Ministerio de la iglesia en toda Judea y Samaria</th> </tr><tr> <th>Hechos 13:1-28:31</th> <th>Ministerio de la iglesia hasta lo último de la tierra (ver Hch 1:8)</th> </tr></table><br /><h6>¿Para quién y por qué se escribió?</h6><p>Lucas le escribió a Teófilo, posiblemente un gentil, quien era un nuevo creyente o alguien que buscaba aprender acerca de Jesús. Teófilo significa «amante de Dios», lo que ha llevado a pensar que el libro se escribió a personas que amaban a Dios. Lucas se propuso que Teófilo y otros lectores supieran que el amor de Dios se extiende hasta abarcar todos los pueblos en todas las naciones, tanto judíos como gentiles.</p><h6>Perspectiva de SourceView</h6><p>Los actores de reparto —texto en azul— que se encuentran en los evangelios comprenden una lista de noventa y nueve individuos o grupos. Ocho de ellos se encuentran en cada uno de los cuatro evangelios. Ellos son los actores indispensables en la historia de Jesús. Estos son: Juan el Bautista, La Multitud, Los Fariseos, Los Líderes Judíos, Poncio Pilato, Los Espectadores en el juicio, Una Sirvienta, y Soldados Romanos.<br />   Luego tenemos treinta y dos miembros del elenco que tienen parlamentos en múltiples evangelios, pero no en todos. Once de ellos hablan en los tres primeros evangelios; uno interviene en todos los evangelios, excepto Lucas, y otro en todos excepto Marcos. Además tenemos seis que hablan solo en Mateo y Marcos; cuatro en Mateo y Lucas; cuatro en Marcos y Lucas; uno en Marcos y Juan, y cuatro en Lucas y Juan. ¿Puedes descubrirlos a todos?<br />   Esto nos deja con cincuenta y nueve miembros del reparto, cada uno con un diálogo en solo un Evangelio. Medita en la contribución única de cada uno de ellos, cómo esta ayuda al lector a descubrir el enfoque distintivo de cada historia del evangelio. Treinta de estos personajes se encuentran en tres evangelios —siete en Mateo, cinco en Marcos y dieciocho en Juan—. ¿Puedes descubrir quiénes son y meditar en por qué el autor decidió incluir sus palabras en su narración del evangelio? Esto significa que cerca de la mitad de estos actores únicos —veintinueve para ser precisos— se encuentran en el Evangelio de Lucas. Estos incluyen:</p><br /><ul><li>Alguien por el Camino</li><li>Alguien de la Multitud</li><li>Algunos Ancianos</li><li>Algunos en Casa de Simón</li><li>Amigos del Oficial Romano</li><li>Cleofas</li><li>Cleofas y Compañero</li><li>Diez Leprosos</li><li>Elisabet</li><li>Gabriel</li><li>Gente en Capernaúm</li><li>Gente en Judea</li><li>Gente en Naín</li><li>El Concilio Judío</li><li>El Criminal Arrepentido</li><li>El Criminal No Arrepentido</li><li>Los Dueños del Burrito</li><li>Los Ejércitos Celestiales</li><li>Los Fariseos y los líderes religiosos</li><li>Pastores cerca de Belén</li><li>Simeón</li><li>Simón el Fariseo</li><li>Un Invitado</li><li>Una Mujer de la Multitud</li><li>Un Líder de la Sinagoga</li><li>Uno de los Diez Leprosos</li><li>Vecinos de Elisabet</li><li>Zaqueo</li><li>El Sacerdote Zacarías</li></ul><br /><p>   Leer las palabras de estos miembros del reparto te ayudará a descubrir la perspectiva especial que Lucas brinda en la historia del evangelio. Trata de descubrir los pasajes que solo se encuentran en Lucas. Luego toma tiempo para meditar en por qué los incluyó en su narrativa. Puede que descubras pequeños tesoros al enfocarte en estos pasajes únicos, como los ilustrados abajo.<br />   <br />   La Respuesta de Fe: el contraste de Lucas entre Zacarías y María<br /><br /><table style='font-size: 15px; max-width: 800px; margin: 0 auto'> <tr> <th colspan='2' style='text-align:center'>Profecía del Nacimiento de Juan<br/>(Lucas 1:5-25)</th> <th colspan='2' style='text-align:center'>Profecía del Nacimiento de Jesús<br/>(Lucas 1:26-56)</th> </tr><tr> <th>1:11</th> <th>Gabriel es enviado</th> <th>1:26</th> <th>Gabriel es enviado</th> </tr><tr> <th>1:13</th> <th>Elisabet dará a luz a un hijo</th> <th>1:31</th> <th>María dará a luz a un hijo</th> </tr><tr> <th>1:17</th> <th>Para preparar a un pueblo</th> <th>1:33</th> <th>Para reinar sobre la casa de Jacob</th> </tr><tr> <th>1:18</th> <th>¿Cómo puede ser? Mi esposa es de edad avanzada</th> <th>1:34</th> <th>¿Cómo podrá suceder esto? Soy una virgen</th> </tr><tr> <th>1:20</th> <th>Porque no creíste</th> <th>1:45</th> <th>Bendita es ella porque creyó</th> </tr><tr> <th>1:22</th> <th>Zacarías queda mudo</th> <th>1:46</th> <th>María glorifica al Señor</th> </tr></table>","path":"/325","Question1":null,"Question2":null,"Question3":null,"Question4":null,"prevPath":"/324","nextPath":"/326"}},"pathContext":{}}

